body {
  background: #333333;
  color: #ffffff;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.weather-container {
  background: transparent;
  color: #fff;
  transition: background 0.5s ease;
  min-height: 100vh;
}

@media only screen and (min-width : 1200px) {
	.weather-forecasts {
		display: flex;
	}
}

/* Night and Day */

.night-bg {
  background: #001942;
}

.night-color {
  color: #001942;
}

.day-bg {
  background: #A8D1FF;
  color: #000000;
}

.day-color {
  color: #68b1ff;
}
.location-box-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.location-suggestions {
  position: absolute;
  background: rgba(250, 250, 250, .9);
  color: #000;
  width: 100%;
  font-weight: 600;
}

.location-suggestions > div {
  border-bottom: solid 1px #000;
  padding: .75em 1em;
}

.location-box {
  background: rgba(90, 90, 90, .8);
  border-bottom: solid 1px rgba(0, 0, 0, .3);
  max-height: 100vh;
  font-size: 1.5em;
  padding: .5em .5em;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.location-box.editing {
  background: rgba(255, 255, 255, .9);
  color: #000;
  border-bottom: solid 1px #000;
}

.location-box-wrapper.night > .location-box {
  background: rgba(0, 0, 0, .9);
  color: #fff;
  border-bottom: solid 1px rgba(255, 255, 255, .9);
}

.location-box-wrapper.night > .location-box.editing {
  background: rgba(255, 255, 255, .9);
  color: #000;
}

.location-icon > img {
  height: 1.5em;
}

.location-string {
  color: #ffffff;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.location-search-field {
  background: transparent;
  border: 0;
  font-size: 1em;
  flex-grow: 1;
  padding: 0;
  margin: 0;
  outline: none;
  display: flex;
  flex-shrink: 1;
  color: inherit;
}

.edit-search-field {
  cursor: pointer;
}
/* Current Weather */

.pending, .error {
  text-align: center;
  margin: 1em 0;
}

/* Current Weather Description */

.current-weather-description {
  margin: .5em 1em;
  padding: 0;
  font-size: 1.5em;
  text-transform: capitalize;
  flex-basis: 100%;
}

/* Current Weather Icons */

.current-weather-icons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.current-weather-icons > div {
  flex-basis: 50%;
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.current-weather-icons > div > svg {
  width: 90%;
  max-width: 75vw;
}

/* Current Weather Temperature */

.current-weather-temp {
  margin: 0 1em;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-between;
}

.current-weather-temp > .temp-live {
  font-size: 6.5em;
}

.current-weather-temp > .temp-hi-lo {
  font-size: 2em;
  display: flex;
  flex-direction: column;
  align-content: stretch;
}

.temp-hi, .temp-lo {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width : 600px) {
  .current-weather {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .current-weather-icons {
    flex-basis: 50%;
    flex-grow: 1;
  }

  .current-weather-temp {
    align-self: stretch;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    font-size: 1.25em;
  }

  .temp-hi-lo {
    flex-direction: row;
    align-content: center;
  }

  .temp-hi, .temp-lo {
    justify-content: center;
  }

  .current-weather-description {
    flex-grow: 1;
    font-size: 2.15em;
  }
}
.temp {
  display: flex;
  padding: 0;
  margin: 0;
  line-height: 1em;
  cursor: pointer;
}

.temp-unit {
  font-size: .75em;
}
.WeatherIcon {
	width: 100%;
	fill: #ffffff;
}

.day-bg .Sun {
	fill: #000000;
}
.Sun {
	-webkit-transform-origin: 50% 50%;
	-webkit-animation-name: spin;
    -webkit-animation-duration: 30000ms;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;

    -moz-transform-origin: 50% 50%;
    -moz-animation-name: spin;
    -moz-animation-duration: 30000ms;
    -moz-animation-iteration-count: infinite;
    -moz-animation-timing-function: linear;

    -ms-transform-origin: 50% 50%;
    -ms-animation-name: spin;
    -ms-animation-duration: 30000ms;
    -ms-animation-iteration-count: infinite;
    -ms-animation-timing-function: linear;

    transform-origin: 50% 50%;
    animation-name: spin;
    animation-duration: 30000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
@-webkit-keyframes spin { 
    from { 
        -webkit-transform: rotate(0deg); 
    } to { 
        -webkit-transform: rotate(360deg); 
    }
}
@keyframes spin { 
    from { 
        -webkit-transform: rotate(0deg); 
                transform: rotate(0deg); 
    } to { 
        -webkit-transform: rotate(360deg); 
                transform: rotate(360deg); 
    }
}
.Moon .outline {
	fill: #000000;
}

.Moon .star {
	fill: #000000;
}

.night-bg .Moon .outline,
.night-bg .Moon .star {
	fill: #ffffff;
}
.Cloud .outline {
	fill: #000000;
}

.night-bg .Cloud .outline {
	fill: #ffffff;
}

.night-bg .Cloud .fill {
	fill: #dddddd;
}

.night-bg .Cloud .outline {
	fill: #ffffff;
}

.Cloud.DarkCloud .outline {
	fill: #000000;
}

.Cloud.DarkCloud .fill {
	fill: #646464;
}

.night-bg .Cloud.DarkCloud .outline {
	fill: #dddddd;
}

.Cloud.Drizzle .fill {
	fill: #cccccc;
}
.Rain {
	fill: #000000;
}

.night-bg .Rain {
	fill: #ffffff;
}
.Lightning {
	fill: #000000;
}

.night-bg .Lightning {
	fill: #ffffff;
}
.Snow .flake .outline {
	fill: #000000;
}

.Snow .flake .fill {
	fill: #ffffff;
}

.night-bg .Snow .flake .outline {
	fill: transparent;
}
.Mist path {
	opacity: .75;
}
.forecast-weather {
}

.pending, .error {
  text-align: center;
  margin: 1em 0;
}

.forecast-weather-day {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: .25em .75em;
  font-size: 1.5em;
}

.forecast-weather-day:nth-child(2n+1) {
  background: rgba(255, 255, 255, .15);
}

.forecast-temperature, .forecast-day-description, .forecast-day-icon, .forecast-weekday {
  margin: 0 .25em;
}

.forecast-temperature {
  flex-shrink: 0;
  flex-basis: 15%;
}

.forecast-day-description {
  flex-grow: 1;
  font-size: .9em;
}

.forecast-day-icon {
  flex-basis: 15%;
  flex-grow: 0;
  /*background: pink;*/
}

.forecast-weekday {
  flex-basis: 15%;
  flex-shrink: 0;
}

.forecast-day-icon svg {
  height: 2em;
}

@media only screen and (min-width : 600px) {
  .forecast-weather {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 1em;
  }

  .forecast-weather-day {
    background: rgba(255, 255, 255, .01);
    flex-basis: 25%;
    padding: 0;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }

  .forecast-temperature, .forecast-day-description, .forecast-day-icon, .forecast-weekday {
    padding: 0 .25em;
  }

  .forecast-day-description {
    flex-grow: 1;
    font-size: 1em;
    margin: .25em 0;
  }

  .forecast-day-icon {    
    flex-basis: auto;
    text-align: center;
    margin: .25em 0;
  }

  .forecast-day-icon > svg {
    width: 50%;
    height: 100%;
  }

  .forecast-weekday {
    background: rgba(0, 0, 0, .125);
    margin: 0px;
    font-size: 1.25em;
    flex-basis: auto;
  }

  .forecast-temperature {
    display: flex;
    flex-basis: auto;
    margin: .25em 0;
  }
}

@media only screen and (min-width : 900px) {
  .forecast-weather-day {
    flex-basis: 33.33%;
  }
}

@media only screen and (min-width : 1200px) {
  .forecast-weather-day {
    flex-basis: 25%;
  }
}

@media only screen and (min-width : 1800px) {
  .forecast-weather-day {
    flex-basis: 12.5%;
  }
}
